}
.icon-box-area .full-icon-box .icon-box .box-icon::before {
    content: "";
    position: absolute;
    border-width: 15px 30px;
    border-style: solid;
    border-color: transparent transparent rgb(24, 43, 73);
    top: -30px;
    left: 0px;
    z-index: 1;
}
 .icon-box-area .full-icon-box .icon-box .box-icon i {
    font-size: 34px;
