    z-index: 111;
}
.icon-box-area .full-icon-box .icon-box .box-icon::after {
    content: "";
    position: absolute;
    border-width: 15px 30px;
    border-style: solid;
    border-color: rgb(24, 43, 73) transparent transparent;
    bottom: -30px;
    left: 0px;
    z-index: 1;
}
